.products {
  padding-top: 60px;
  padding-bottom: 60px;
  margin: 0 auto;
}

.products__feature {
  font-family: 'Titan One';
  font-size: 18px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;
  color: #ffa5ba;

  display: block;
  margin-bottom: 15px;
}

.products__title {
  font-family: 'Titan One';
  font-weight: 400;
  font-size: 34px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #d41443;

  margin-bottom: 8px;
}

.products-list {
  display: flex;
  flex-wrap: wrap;
}

.products-list__item {
  border-radius: 24px;
  height: 489px;
  width: 280px;
  padding-top: 160px;
  padding-left: 40px;
  padding-right: 40px;
}

.products__ice-cream {
  position: relative;
  background-color: #ffb8ca;
  &::before {
    position: absolute;
    top: -140px;
    left: 0;
    display: block;
    content: '';
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url('../images/products/mob/icecream-mob.png');
    height: 280px;
    width: 280px;
  }
}

