.products__ice-coffee {
  background-color: #f0d1a5;
}

.products__milkshakes {
  background-color: #c2e297;
}

.products-list__title {
  font-family: 'Titan One';
  font-weight: 400;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #ffffff;

  margin-bottom: 70px;
}

.products-list__text {
  font-family: 'DM Sans';
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.04em;
  color: #ffffff;

  margin-bottom: 32px;
}

.products-list__button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background-color: #ffffff;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.products-list_button-icon {
  fill: #d41443;
}
